'use client';

import { UserInfoInputs } from '@mintlify/validation';

const SERVERVARS_LOCALSTORAGE_KEY = 'mintlify-server-variables';

export type ServerVariableStore = Record<string, Record<string, string>>;

const isValidServerVariableStore = (info: unknown): info is ServerVariableStore => {
  if (typeof info !== 'object' || info == null) {
    return false;
  }

  const infoObj = info as Record<string, unknown>;
  return Object.values(infoObj).every(
    (inputMap) =>
      typeof inputMap === 'object' &&
      inputMap !== null &&
      Object.values(inputMap).every((input) => typeof input === 'string')
  );
};

export const loadServerVariableStore = (): ServerVariableStore => {
  const inputString = localStorage.getItem(SERVERVARS_LOCALSTORAGE_KEY);

  if (inputString) {
    try {
      const inputs = JSON.parse(inputString);
      if (isValidServerVariableStore(inputs)) {
        return inputs;
      }
    } catch (e) {
      console.log(`unable to parse stored server variables: ${e}`);
    }
  }

  return {};
};

export const storeServerVariables = (
  baseUrl: string,
  serverVariables: Record<string, string>,
  userInfoInputs: UserInfoInputs | undefined
) => {
  // only store the variables that differ from those in user info
  const meaningfulServerVariables = Object.fromEntries(
    Object.entries(serverVariables).filter(([name, value]) => {
      return value && userInfoInputs?.server?.[name] !== value;
    })
  );

  const existingServerVariableStore = loadServerVariableStore();
  const newServerVariableStore = {
    ...existingServerVariableStore,
    [baseUrl]: meaningfulServerVariables,
  };
  const serverVariableString = JSON.stringify(newServerVariableStore);
  localStorage.setItem(SERVERVARS_LOCALSTORAGE_KEY, serverVariableString);
};
